.contact-preview{
    box-shadow: 0 0 2px 1px rgba(139,139,139,0.3);
    cursor: pointer;
    display: flex;
    // flex-direction: column;
    align-items: center;

    justify-content: space-between;
    padding: 5px;
    border-radius: em(20px);
    margin-block-end: 10px;
    >* {
        text-align: center;
        padding-inline: 10px;
    }
    :first-child{
        display:flex;
        align-items: center;
        justify-content: space-between;
    }
    img{
        width:50px;
        object-fit: contain;
        margin-inline-end:10px;
    }
}
.contact-preview:hover{
    background-color: greenyellow;
}